.addtodo_overlay {
    display: flex;
    width: 1200px;
    margin: 10px auto;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.addtodo_informer {

}
.addtodo_form {
    display: flex;
    width: 800px;
    margin: 0 auto;
}
.addtodo_form input {
    font-size: 16px;
    padding: 10px 10px 5px 15px;
    width: 600px;
    border: none;
    border-bottom: 2px solid darkslategray;
}

.addtodo_form button {
    font-size: 16px;
    padding: 5px 10px;
    padding-right: 15px;
    margin-left: auto;
    border: none;
    background-color: darkslategray;
    color: whitesmoke;
    text-transform: uppercase;
    font-weight: 600;
    transition: background-color 0.5s;
}
.addtodo_form button:hover {
    background-color: teal;
    cursor: pointer;
}